// Code de style pour la partie du services dans le site web

// Placement de la partie services avec un display grid de deux colonnes et de quatre rangés
.ListeProjet {
    margin: 0px auto;
    width: 85vw;
    display: flex;
    background: slategray;
    justify-content: center;
    list-style: none;
    border-style: solid;

    // Placement et personnalisation du titre qui se trouve au milieu dans la partie Projet.
    h1 {
        margin: 24px 0 0 104px;
        color: white;
        font-family: "Proxima Nova";
        font-size: 70px;
        font-weight: 800;
        letter-spacing: 0;
        line-height: 50px;
        grid-area: cell2;
    }

   .Liste{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 26px 313px 0 0px;
    padding: 0;
   }

li{
    width: 400px;
    margin: 0 0 0 0px;
    padding: 145px 0 0 37px;
}


    // Placement et personnalisation du premier sous-titre.
    .analyse {
    margin: 140px 0px 0px -55vw;
    border-bottom: solid;
    color: white;
    font-family: "Proxima Nova";
    font-size: 25px;
    font-weight: 600;
    position: absolute;
    letter-spacing: 2px;
    line-height: 29px;
    }
    
    // Placement et personnalisation du premier sous-titre.
    .jeu {
        margin: 532px 0px 0px -55vw;
        border-bottom: solid;
        color: white;
        font-family: "Proxima Nova";
        font-size: 25px;
        font-weight: 600;
        position: absolute;
        letter-spacing: 2px;
        line-height: 29px;
        }
        // Placement et personnalisation du premier sous-titre.
    .model {
        margin: 1025px 0px 0px -55vw;
        border-bottom: solid;
        color: white;
        font-family: "Proxima Nova";
        font-size: 25px;
        font-weight: 600;
        position: absolute;
        letter-spacing: 2px;
        line-height: 29px;
        }
        // Placement et personnalisation du premier sous-titre.
    .design {
        margin: 1506px 0px 0px -55vw;
        border-bottom: solid;
        color: white;
        font-family: "Proxima Nova";
        font-size: 25px;
        font-weight: 600;
        position: absolute;
        letter-spacing: 2px;
        line-height: 29px;
        }
}

